import AddTrainingToDifferentDayModal from "../modify_training/modals/AddTrainingToDifferentDayModal";

const yup = require("yup");

export const createGoalValidation = yup.object().shape({
    finishDate: yup.string().min(8, "Wprowad poprawn date").required('Wybierz date'),
    goal: yup.string().min(2, "Cel jest za krtki").max(100, "Cel jest za dugi wykorzystaj opis").required('Pole jest wymagane'),
    description: yup.string(),
});

export const updateGoalValidation = yup.object().shape({
    finishDate: yup.string().min(8, "Wprowad poprawn date").required('Wybierz date'),
    goal: yup.string().min(2, "Cel jest za krtki").max(100, "Cel jest za dugi wykorzystaj opis").required('Pole jest wymagane'),
    description: yup.string(),
    completed: yup.boolean().required()
});


export const settingsDimensionValidation = yup.object().shape({
    id: yup.number().required(),
    weight: yup.boolean().required(),
    growth: yup.boolean().required(),
    left_biceps: yup.boolean().required(),
    right_biceps: yup.boolean().required(),
    left_forearm: yup.boolean().required(),
    right_forearm: yup.boolean().required(),
    left_leg: yup.boolean().required(),
    right_leg: yup.boolean().required(),
    bodyfat: yup.boolean().required(),
});

export const createDimensionValidation = yup.object().shape({
    weight: yup.number().nullable(false).required("Waga jest wymagana"),
    growth: yup.number().nullable(true),
    left_biceps: yup.number().nullable(true),
    right_biceps: yup.number().nullable(true),
    left_forearm: yup.number().nullable(true),
    right_forearm: yup.number().nullable(true),
    left_leg: yup.number().nullable(true),
    right_leg: yup.number().nullable(true),
    bodyfat: yup.number().nullable(true),
})

export const createMultiSeriesValidation = yup.object().shape({
    exercise: yup.object().nonNullable("Wybierz wiczenie").required("Wybierz wiczenie"),
    extra_weight: yup.number().max(10000, "Nie no tyle to nie. Tutaj wstaw tego mema. :)").required("Musisz uzupeni wage").typeError("Dodatkowa waga musi by liczb"),
    rest: yup.number().integer().max(10000).required("Musisz uzupeni czas przerwy").typeError("Odpoczynek musi by liczb"),
    reps: yup.number().required("Musisz uzupeni liczbe powtrze").typeError("Liczba powtrze musi by liczb"),
    concentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    pause_after_concentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    eccentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    pause_after_eccentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    series_count: yup.number().integer().min(1, "Liczba jest za maa").max(50, "Liczba serii jest za dua").typeError("Liczba serii musi by liczb cakowit od 0 od 100"),
})

export const createSingleSeriesValidation = yup.object().shape({
    exercise: yup.object().nonNullable("Wybierz wiczenie").required("To pole jest wymagane"),
    extra_weight: yup.number().max(10000, "Nie no tyle to nie. Tutaj wstaw tego mema. :)").required("Musisz uzupeni wage").typeError("Dodatkowa waga musi by liczb"),
    rest: yup.number().integer().max(10000).required("Musisz uzupeni czas przerwy").typeError("Odpoczynek musi by liczb cakowit"),
    reps: yup.number().required("Musisz uzupeni liczbe powtrze").typeError("Liczba powtrze musi by liczb"),
    concentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    pause_after_concentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    eccentric_phase: yup.number().typeError("Fazy musz by liczbami"),
    pause_after_eccentric_phase: yup.number().typeError("Fazy musz by liczbami"),
})


export const createTrainingValidation = yup.object().shape({
    name: yup.string().min(2, "Nazwa jest za krtka").max(100, "Nazwa jest za duga wykorzystaj opis").required('Pole nazwa jest wymagane'),
    date: yup.string().min(2, "Musisz wybra date").required("Pole data jest wymagane"),
    description: yup.string().max(5000, "Opis jest za dugi"),
    multi_series: yup.array().min(1, 'Przynajmniej jedna seria jest wymagana')

})

export const loginUserValidation = yup.object().shape({
    username: yup.string().min(2, "Nazwa uytkownika jest za krtka").max(100, "Nazwa uytkownika jest za duga ").required('Pole username jest wymagane'),
    password: yup.string().min(2, "Haso jest za krtkie").max(100, "Haso jest za dugie").required('Pole haso jest wymagane'),
})

export const registerUserValidation = yup.object().shape({
    username: yup.string().min(2, "Nazwa uytkownika jest za krtka").max(100, "Nazwa uytkownika jest za duga ").required('Pole username jest wymagane'),
    email: yup.string().email('Podany adres e-mail nie jest prawidowy.').required('Adres e-mail jest wymagany.'),
    password: yup.string().min(2, "Haso jest za krtkie").max(100, "Haso jest za dugie").required('Pole haso jest wymagane'),
    password2: yup.string()
        .oneOf([yup.ref('password'), null], 'Hasa musz by identyczne.')
        .required('Potwierdzenie hasa jest wymagane.'),
})

export const createExerciseModalValidation = yup.object().shape({
    name: yup.string().required('Stare haso jest wymagane'),
    password: yup.string().min(2, "Haso jest za krtkie").max(100, "Haso jest za dugie")
        // .min(2, "Nazwa wiczenia jest za krtka")
})

export const changePasswordValidation = yup.object().shape({
    old_password: yup.string().required('Pole stare haso jest wymagane'),
    new_password1: yup.string().min(2, "Haso jest za krtkie").max(100, "Haso jest za dugie").required('Pole haso jest wymagane'),
    new_password2: yup.string()
        .oneOf([yup.ref('new_password1'), null], 'Hasa musz by identyczne.')
        .required('Potwierdzenie hasa jest wymagane.'),
})

export const addTrainingToDifferentDayValidation = yup.object().shape({
    name: yup.string().min(2, "Nazwa treningu jest za krtka").required("Pole name nie moe by puste"),
    date: yup.string().min(8, "Data si nie zgadza").required("Pole data nie moe by puste")
})